/* ==================== Media queries ======================= */
/* Large Devices, Wide Screens */
@media only screen and (max-width : 1700px) {
  .main-img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media only screen and (max-width : 1300px) {
  html {
    font-size: 8px;
  }
}
@media only screen and (max-width : 1199px) {
  .main-img {
    left: 40%;
  }
  .desktop-header-nav ul {
    font-size: 15px;
  }
  .header-logo {
    max-width: 520px;
  }
  .header-link-order {
    margin-left: 35px;
  }
  .catalog-wrapper {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .categories-accordion-wrapper,
  .categories-accordion {
    width: 100%;
  }
  .categories-accordion > li {
    display: inline-block;
    width: 33%;
  }
  .categories-accordion li ul {
    position: absolute;
    width: 100%;
    z-index: 2;
    border: 8px solid #fff;
  }
  .categories-accordion > li h4 {
    line-height: 40px;
  }
  .new-mark {
    top: 5px;
  }
  .cat-inline-list {
    justify-content: center;
  }
  .cat-inline-list li {
    flex-basis: auto !important;
    margin: 10px 15px;
  }
  .categories-accordion > li h4,
  .extended ul li a {
    font-size: 13px;
  }
  .categories-accordion .hit h4 {
    font-size: 14px;
  }
  .extended ul li a {
    height: 40px;
    line-height: 20px;
  }
  .categories-accordion > li h4 > span,
  .categories-accordion > li h4 > a {
    padding: 0 8px;
  }
  .catalog-items-container {
    padding-left: 0;
  }
  .product-item {
    width: 24%;
  }
  .about-balloons-content {
    width: 100%;
  }
  .delivery-features-container {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .deliv-features-left {
    flex-basis: 100%;
    max-width: 100%;
    width: 100%;
    padding: 0;
    &:after {
      width: 80%;
      height: 1px;
      top: auto;
      bottom: 0;
      left: 10%;
      right: 10%;
    }
  }
  .deliv-features-right {
    flex-basis: 100%;
    max-width: 100%;
    
  }
  .deliv-feature {
    flex-basis: 50%;
    width: 50%;
  }
  .deliv-feature {
    padding: 30px 10px 20px;
  }
  .footer-logo {
    max-width: 200px;
  }
  .footer-nav a {
    font-size: 12px;
  }
  .footer-link-phone {
    font-size: 14px;
    margin-right: 20px;
  }
}
@media only screen and (max-width: 1099px) {
  .product-item {
    width: 31%;
    flex-basis: 31%;
  }

}
/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .desktop-header-nav {
    display: none;
  }
  .mob-menu-btn {
    display: block;
  }
  .main-img {
    min-width: 1500px;
  }
  .about-text {
    padding-left: 0;
  }
  .product-text {
    padding-left: 10px;
    padding-right: 10px;
  }
  .count-container {
    padding: 0 35px;
  }
  .mult {
    font-size: 12px;
    vertical-align: middle;
  }
  .count-container input[type="number"] {
    width: 50px;
    margin: 0 10px;
    font-size: 12px;
    vertical-align: middle;
  }
  .minus, .plus {
    font-size: 20px;
    vertical-align: middle;
  }
  .set-item {
    padding-bottom: 160px;
  }
  .set-form label {
    margin-bottom: 17px;
  }
  .locations-list a {
    font-size: 14px;
  } 
  .map-block {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
    ul {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      -webkit-flex-wrap: wrap;
      -moz-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
      -o-flex-wrap: wrap;
      flex-wrap: wrap;
      justify-content: center;
      li {
        flex-basis: 230px;
        margin-right: 40px ;
      }
    }
  }
  .location {
    height: 45px;
  }
  .locations-tarifs {
    flex-basis: 100%;
    margin-right: 0;
  }
  .locations-tarifs h3 {
    text-align: center;
  }
  .map-container {
    width: 100%;
    height: 600px;
    max-height: 90vh;
  }
  .contacts-container {
    padding: 4rem 6rem;
  }
  .footer-top {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .footer-nav {
    width: 100%;
  }
  .footer-top-right {
    margin: 0 15px 15px 0;
  }
  .footer-bottom {
    text-align: left;
  }
  .payments-methods li {
    margin-left: 30px;
    &:first-child {
      margin-left: 0;
    }
  }
}
/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
  html {
    font-size: 5px;
  }
  h2 {
    font-size: 30px;
  }
  .header {
    min-height: 480px;
  }
  .header:after {
  }
  .main-img {
    min-width: 1300px;
    left: 20%;
  }
  .categories-accordion > li {
    width: 49.8%;
  }
  .adv-item{
    width: 50%;
  }
  .call-us-parag,
  .contact-us-parag {
    font-size: 18px;
  }
  .cat-inline-list li a {
    height: auto;
    line-height: 1.2;
    white-space: normal;
    letter-spacing: 1px;
  }
  .product-item {
    width: 48%;
    flex-basis: 48%;
  }
  .about-block {
    padding: 20px;
  }
  .locations-container {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .locations-list {
    flex-basis: 100%;
    width: 100%;
  }
  .contacts-container {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
    justify-content: center;
  }
  .mail-container,
  .phone-container {
    max-width: 100%;
    padding-left: 80px;
    a {
      font-size: 16px;
    }
  }
}
@media only screen and (max-width : 600px) {
  .header-logo {
    max-width: 95vw;
  }
  .header-link {
    font-size: 14px;
  }
  .header-link-phone {
    padding-left: 0;
  }
  .header-link-phone .header-link-icon {
    display: none;
  }
  .header-link-phone a {
    font-size: 16px;
    &:first-of-type {
      margin-bottom: 7px;
    }
  }
  .header-text {
    line-height: 1.2;
    font-size: 15px;
    p {
      margin-bottom: 10px;
    }
  }
  .form-close {
    right: -40px;
  }
  .product-item {
    width: 96%;
    flex-basis: 96%;
    max-width: 300px;
    margin: 0 auto 20px;
  }
  .map-block ul li {
    flex-basis: 100%;
    margin: 0 auto;
  }
  .attend-text p {
    line-height: 1.5;
  }
  .footer-nav ul {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
    li {
      width: 100%;
      a {
        display: inline-block;
      }
    }
  }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
  h2 {
    font-size: 28px;
  }
  .header {
    min-height: 490px;
  }
  .main-img {
    left: -10%;
    min-width: 1200px;
  }
  .header-top-right {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .header-link {
    flex-basis: 100%;
    margin-bottom: 15px;
  }
  .header-link-order .header-link-icon {
    display: none;
  }
  .header-link-order {
    padding-left: 0;
    margin-left: 0;
  }
  .header-center {
    top: 24%;
  }
  .order-btn {
    max-width: 100%;
  }
  .header-call-btn {
    display: block;
    margin: 0 auto;
  }
  .categories-accordion > li {
        width: 100%;
  }
  .deliv-features-left,
  .deliv-features-right {
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .deliv-feature {
    width: 100%;
    flex-basis: 100%;
  }
  .self-delivery {
    height: 100px;
  }
  .self-delivery h2 {
    padding-right: 0;
    font-size: 20px;
  }
  .attend-text p {
    text-indent: 20px;
  }
  .form-close {
    right: 0;
    top: -37px;
    width: 20px;
    height: 20px;
  }
}
/* Custom, iPhone Retina */
@media only screen and (max-width : 375px) {
  .adv-item{
    width: 100%;
  }
}